<template>
  <div class="forgetpassword">
    <van-nav-bar title="" left-text="" @click-left="back" fixed></van-nav-bar>

    <van-field v-model="email" type="text" label="" />
    <van-button type="danger" @click="get"></van-button>

    <van-field v-model="phone" type="text" label="" />
    <van-field v-model="password" type="password" label="" />
    <van-field v-model="emailma" type="text" label="" />

    <van-button type="danger" @click="set"></van-button>
  </div>
</template>

<script>
import { utils } from "../assets/js/utils";
import { Dialog } from "vant";
import { validForm } from "../assets/js/validForm";
export default {
  data() {
    return {
      email: "",
      phone: "",
      password: "",
      emailma: "",
    };
  },
  methods: {
    back() {
      this.$router.push({ name: "Login" });
    },
    set() {
      let o = {
        phone: {
          value: this.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "",
        },

        password: {
          value: this.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "",
        },
        password: {
          value: this.emailma,
          reg: /^[0-9]*$/,
          errorMsg: "",
        },
      };
      // 
      if (!validForm.valid(o)) {
        //
        return;
      }
      Dialog.confirm({
        title: "",
        message: "",
      })
        .then(() => {
          ;
          this.each();
        })
        .catch(() => {
          // on cancel
          ;
        });
    },
    // 
    setpassworld() {
      let data = utils.queryString({
        appkey: this.appkey,
        password: this.password,
        phone: this.phone,
      });

      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/retrievePassword",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          ;
          this.$toast(result.data.msg);
          this.$router.push({ name: "Login" });
        })
        .catch((err) => {
          this.$toast.clear();
          ;
        });
    },
    // 
    get() {
      let o = {
        password: {
          value: this.email,
          reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
          errorMsg: "",
        },
      };
      if (!validForm.valid(o)) {
        //
        return;
      }

      let data = utils.queryString({
        appkey: this.appkey,
        email: this.email,
      });

      this.axios({
        method: "POST",
        url: "/emailValidCode",
        data,
      })
        .then((result) => {
          ;
        })
        .catch((err) => {
          ;
        });
    },
    // 
    each() {
      let data = utils.queryString({
        appkey: this.appkey,
        validCode: this.emailma,
      });
      this.axios({
        method: "POST",
        url: "/checkValidCode",
        data
      }).then(result=>{

        ;
        if (result.data.code=="K002") {
           this.$toast(result.data.msg);
        }
        if (result.data.code=="K001") {
          this.setpassworld()
        }
      }).catch(err=>{
        ;
      })
    },
  },
};
</script>

<style lang="less" scoped>
.forgetpassword {
  padding: 50px 10px 0;
  background-color: #f5f5f5;
  min-height: 100vh;
  box-sizing: border-box;
  /deep/ .van-field {
    border-bottom: 1px solid #d7d7d7;
    margin-bottom: 5px;
  }
  /deep/ .van-button {
    background-color: #0833bc;
    border-color: #0833bc;
    margin: 10px;
  }
}
</style>