<template>
  <div class="Login">
    
    <van-nav-bar left-arrow @click-right="back" class="login-top">
      <template #left>
        <div class="logo iconfont">&#xe50c;</div>
        <div class="logotext">Luckin Coffee</div>
      </template>
      <template #right>
        <div class="back"></div>
      </template>
    </van-nav-bar>
    <div class="login-text">
      <div class="welcome">!</div>
      <div class="english">Please login to your accounts</div>
    </div>
    <div class="login-input">
      <van-form>
        <van-field name="" label="" placeholder="" v-model="userLogin.phone" />
        <van-field
          v-model="userLogin.password"
          :type="isPassword? 'password':'text'"
          name=""
          label=""
          placeholder=""
          :right-icon="isPassword? 'closed-eye':'browsing-history'"
          @click-right-icon="down"
        />
        <div class="pass clearfix">
          <div class="fr" @click="forget">?</div>
        </div>
        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit" @click="denglu"></van-button>
          <van-button type="default" class="register" @click="showPopup"></van-button>
          <van-popup
            class="toregister"
            v-model="show"
            position="bottom"
            round
            overlay-class="mask"
            :style="{ height: '30%' }"
          >
            <van-form>
              <div class="logintrue">
                <div class="comeback">
                  <van-icon name="cross" @click="showPo" />
                </div>
                <div class="nametop"></div>
              </div>
              <van-field
                name=""
                label=""
                placeholder=" (+86)"
                v-model="userRegister.phone"
              />
              <van-field v-model="userRegister.nickName" name="" label="" placeholder="" />
              <van-field
                name=""
                label=""
                v-model="userRegister.password"
                @click-right-icon="down2"
                :type="isRegister? 'password':'text'"
                placeholder=""
                :right-icon="isRegister? 'closed-eye':'browsing-history'"
              />
              <div style="margin: 16px;">
                <van-button type="default" class="register" @click="zhuce"></van-button>
              </div>
            </van-form>
          </van-popup>
        </div>
       
      </van-form>
    </div>
  </div>
</template>

<script>
import "../assets/less/login.less";

// 
import { utils } from "../assets/js/utils";

// 
import { validForm } from "../assets/js/validForm";
export default {
  name: "Login",
  data() {
    //
    return {
      // 
      userLogin: {
        phone: "",
        password: "",
      },
      // 
      userRegister: {
        phone: "",
        nickName: "",
        password: "",
      },
      isPassword: true,
      isRegister: true,
      show: false,
    };
  },
  methods: {
    showPopup() {
      // 
      this.show = true;
    },
    showPo() {
      // 
      this.show = false;
    },
    back() {
      // 
      ;
     this.$router.push({ name: "Shouye" });
    },
    down() {
      // 
      this.isPassword = !this.isPassword;
    },
    down2() {
      // ;
      this.isRegister = !this.isRegister;
    },
    // 
    denglu() {
      // 
      let userInfo = this.userLogin;

      //  ,
      let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "",
        },
        nickName: {
          value: userInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg: "",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "",
        },
      };
      // 
      if (!validForm.valid(o)) {
        //
        return;
      }
      // 
      this.$toast.loading({
        message: "...",
        //
        forbidClick: true,

        //, 0
        duration: 0,

        loadingType: "spinner",
      });

      let info = Object.assign({}, userInfo);

      info.appkey = this.appkey;
      // 
      let data = utils.queryString(info);

      this.axios({
        method: "POST",
        url: "/login",
        data,
      })
        .then((result) => {
          // 
          this.$toast.clear();
          if (result.data.code == 200) {
            //  
            localStorage.setItem("CSTK", result.data.token);
            // 
            this.$router.push({ name: "My" });
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          ;
        });
    },
    // 
    zhuce() {
      // 
      let userInfo = this.userRegister;

      //  ,
      let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "",
        },
        nickName: {
          value: userInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg: "",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "",
        },
      };

      // 
      if (!validForm.valid(o)) {
        //
        return;
      }
      // 
      this.$toast.loading({
        message: "...",
        //
        forbidClick: true,

        //, 0
        duration: 0,

        loadingType: "spinner",
      });

      //
      // .,
      let info = Object.assign({}, userInfo);

      info.appkey = this.appkey;
      // 
      let data = utils.queryString(info);

      ;
      //  
      this.axios({
        method: "POST",
        url: "/register",
        //
        data,
      })
        .then((result) => {
          // 
          // 
          this.$toast.clear();

          // 
          ;
          if (result.data.code == 100) {
            ;
            this.show = false;
            //

            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
            for (let key in userInfo) {
              userInfo[key] = "";
            }
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 
    forget(){
      this.$router.push({name:"Forgetpassword"})
    }
  },
};
</script>

<style lang="less" scoped>
@font-face {
  font-family: "../assets/font/iconfont";
  src: url("../assets/font/iconfont.eot");
  src: url("../assets/font/iconfont.eot?#iefix") format("embedded-opentype"),
    url("../assets/font/iconfont.woff2") format("woff2"),
    url("../assets/font/iconfont.woff") format("woff"),
    url("../assets/font/iconfont.ttf") format("truetype"),
    url("../assets/font/iconfont.svg#iconfont") format("svg");
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 40px;
  font-style: normal;
  color: #0833bc;
  // margin-top: -30px;
  -moz-osx-font-smoothing: grayscale;
}
</style>