<template>
  <div class="site">
    <div class="dingdantitle">
      <van-nav-bar title="" :left-text="''" @click-left="back" fixed></van-nav-bar>
    </div>
    <van-address-list
      :switchable="cheak"
      v-model="chosenAddressId"
      :list="list"
      default-tag-text=""
      @add="onadd"
      @edit="onedit"
     
    />
    <van-popup v-model="show">
      <van-address-edit
        show-postal
        show-delete
        show-set-default
        :area-columns-placeholder="['', '', '']"
        :area-list="areaList"
        :address-info="addressInfo"
        @save="newAddress"
        @delete="deleteAddress"
      />
    </van-popup>

    <van-popup v-model="setshow">
      <van-address-edit
        show-postal
        show-delete
        show-set-default
        :area-columns-placeholder="['', '', '']"
        :area-list="areaList"
        :address-info="addressInfo"
        @save="setAddress"
        @delete="deleteAddress"
      />
    </van-popup>
  </div>
</template>

<script>
import areaList from "../assets/js/arealist";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      // 
      chosenAddressId: "1",
      isshow: false,
      show: false,
      cheak: false,
      setshow: false,
      // 
      sids: "",
      // 
      resultdata: {},
      // 
      sum: 0,
      areaList,
      //
      addressInfo: {
        id: "",
        name: "",
        tel: "",
        province: "",
        city: "",
        county: "",
        addressDetail: "",
        areaCode: "",
        postalCode: "",
        isDefault: false,
      },
      chosenAddressId: "1",
      list: [],
      // 
      selectaddress: {},
      // 
      address: "",
    };
  },
  created() {
    this.each();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    // 
    newAddress(address) {
      let content = Object.assign({}, address);
      //token
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      delete content.id;
      delete content.country;

      content.isDefault = Number(content.isDefault);
      content.appkey = this.appkey;
      content.tokenString = tokenString;

      //
      let data = utils.queryString(content);

      ;

      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/addAddress",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          ;
          this.each();
        })
        .catch((err) => {
          this.$toast.clear();
          ;
        });
    },
    // 
    deleteAddress(address) {
      if (!address.aid) {
        return;
      }

      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }
      //
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        aid: address.aid,
      });

      ;

      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/deleteAddress",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          this.addressInfo = {
            id: "",
            name: "",
            tel: "",
            province: "",
            city: "",
            county: "",
            addressDetail: "",
            areaCode: "",
            postalCode: "",
            isDefault: false,
          };
          ;
          this.each();
        })
        .catch((err) => {
          this.$toast.clear();
          ;
        });
    },
    // 
    onadd() {
      this.show = !this.show;
      //  
      this.addressInfo = {
        id: "",
        name: "",
        tel: "",
        province: "",
        city: "",
        county: "",
        addressDetail: "",
        areaCode: "",
        postalCode: "",
        isDefault: false,
      };
    },
    // 
    onedit(item) {
      ;
      this.setshow = !this.setshow;
      this.addressInfo = item;
    },
    setAddress(address) {
      let content = Object.assign({}, address);
      //token
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      delete content.id;
      delete content.country;

      content.isDefault = Number(content.isDefault);
      content.appkey = this.appkey;
      content.tokenString = tokenString;
      content.aid = this.addressInfo.aid;

      //
      let data = utils.queryString(content);

      ;

      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/editAddress",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          ;
          this.each();
        })
        .catch((err) => {
          this.$toast.clear();
          ;
        });
    },
    // 
    each() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          ;

          result.data.result.map((v) => {
            this.address = v.city + v.county + v.addressDetail;
            v.address = v.city + v.county + v.addressDetail;
          });

          this.list = result.data.result;
        })
        .catch((err) => {
          this.$toast.clear();
          ;
        });
    },
  },
};
</script>

<style lang="less" scoped>
.site {
  min-height: 100vh;
  background-color: #f1f3f4;
  box-sizing: border-box;
  padding: 10px;
  padding-top: 46px;
  /deep/ .van-popup {
    min-width: 100%;
  }
  /deep/ .van-address-list__bottom{
      background-color:#f1f3f4 ;
  }
}
</style>