<template>
  <div class="My">
    <div class="mask"></div>
    <div class="My-top"></div>
    <div class="My-head">
      <div class="info-title">
        <div class="info-img">
          <div v-if="myinfos.userImg==null " @click="gologin"></div>
          <img :src="myinfos.userImg" v-else alt />
        </div>
        <div class="info-name">
          <div class="name">{{myinfos.nickName}}</div>
       
          <div class="desc">{{myinfos.desc==""? '':myinfos.desc}}</div>
        </div>
      </div>
    
      <div class="inform">
        <van-notice-bar
          mode="closeable"
          color="#1989fa"
          background="#ecf9ff"
          left-icon="volume-o"
          v-if="myinfos.userImg!=null "
        >5000.</van-notice-bar>
      </div>
    </div>
    <div class="infoset">
      <van-cell
        :title="value.title"
        is-link
     
        :to="{name:value.name}"
        v-for="(value,index) in infomains"
        :key="index"
      />
    </div>
  </div>
</template>

<script>
import "../../assets/less/My.less";

export default {
  name: "my",
  data() {
    return {
      myinfos: {},
      infomains: [
        { title: "", name: "Personal" },
        { title: "", name: "Dingdan" },
        { title: "", name: "Collect" },
        { title: "", name: "Site" },
        { title: "", name: "Safety" },
      ],
    };
  },
  created() {
    this.myinfo();
  },
  methods: {
    gologin() {
      this.$router.push({ name: "Login" });
    },
    // 
    myinfo() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return ;
      }
      this.axios({
        methos: "GET",
        appkey: this.appkey,
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          ;
          if (result.data.code == "A001") {
            this.myinfos = result.data.result[0];
          }
        })
        .catch((err) => {
          ;
        });
    },
    gofrom() {
      this.$router.push({ name: "Dingdan" });
    },
    gocollect() {
      this.$router.push({ name: "Collect" });
    },
    goSite() {
      this.$router.push({ name: "Site" });
    },
  },
};
</script>

<style lang="less" scoped>

</style>