<template>
  <div class="Shouye">
    <van-popup v-model="show" position="bottom">
          <div class="product">
      <div class="products" v-for="(value,index) in eachdata" :key="index">
        <div class="pro-box">
          <div class="pro-img" @click="toggles(value)">
            <img class="auto-img" :src="value.smallImg" alt />
          </div>
          <div class="pro-info">
            <div class="pro-text fl">
              <div class="ch-name">{{value.name}}</div>
              <div class="en-name">{{value.enname}}</div>
              <div class="pro-price">{{value.price}}</div>
            </div>
            <div class="plus fr" @click="toggles(value)">
              <div>+</div>
            </div>
          </div>
        </div>
      </div>
    </div>

    </van-popup>

    <div class="shouye-title">
      <van-nav-bar>
        <template #left>{{timeText}},sir</template>

        <template #right>
          <van-search
            v-model="value"
            shape="round"
            background="#ffffff"
            @blur="each"
            placeholder=""
          />
        </template>
      </van-nav-bar>
      <van-swipe :autoplay="3000">
        <van-swipe-item v-for="(value, index) in daydata" :key="index" @click="toggles(value)">
          <img :src="value.bannerImg" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="shouye-header">
      <div class="shouye-banner">
        <div class="transparents"></div>
        <van-nav-bar>
          <template #right>
            <div>
              <p>Hi,&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Jack</p>
              <p></p>
            </div>
          </template>
        </van-nav-bar>
        <div class="mode" @click="tishi">
          <div class="take-out">
            <div></div>
            <div class="iconfont">&#xe608;</div>
          </div>

          <div class="pick-up">
            <div></div>
            <div class="iconfont">&#xe51c;</div>
          </div>
        </div>
        <div class="fours">
          <div v-for="(value,index) in fours" :key="index" class="list" @click="tishi">
            <div>
              <div class="iconfont" v-html="value.icon"></div>
              <div class="text">{{value.title}}</div>
            </div>
          </div>
        </div>
        <div class="recommend">
          <h3></h3>
          <div class="auto">
            <div class="recommend-ban">
              <div v-for="(value, index) in tuijiandata" :key="index" @click="toggles(value)">
                <img :src="value.smallImg" />
                <h4>{{value.enname}}</h4>
                <h4>{{value.name}}</h4>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { Toast } from "vant";
import "../../assets/less/Shouye.less";
export default {
  name: "Shouye",
  data() {
    return {
      show: false,
      eachdata: "",
      value: "",
      fours: [
        {
          title: "",
          icon: "&#xe621;",
        },
        {
          title: "ta",
          icon: "&#xe6b8;",
        },
        {
          title: "",
          icon: "&#xe62e;",
        },
        {
          title: "",
          icon: "&#xe607;",
        },
      ],
      daydata: [],
      tuijiandata: [],
      timeText: "",
    };
  },
  created() {
    this.getTime();
    this.getbannerData();
    this.getHotProducts();
  },
  methods: {
    getTime() {
      let hours = new Date().getHours();
      if (hours >= 6 && hours < 12) {
        this.timeText = "";
      } else if (hours >= 12 && hours < 18) {
        this.timeText = "";
      } else if (hours >= 18 && hours < 24) {
        this.timeText = "";
      } else {
        this.timeText = "";
      }
    },
    // 
    getbannerData() {
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/banner",
        //getparams, post,data
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();
          ;
          if (result.data.code == 300) {
            this.daydata = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //
    getHotProducts() {
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        //getparams, post,data
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 500) {
            this.tuijiandata = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    toggles(value) {
      let pid = value.pid;
      this.$router.push({ name: "Detail", query: { pid } });
    },
    tishi() {
      Toast.fail(",");
    },
    // 

    each() {
      if (this.value == "") {
        return;
      }
      this.axios({
        method: "GET",
        url: "/search",
        params: {
          appkey: this.appkey,
          name: this.value,
        },
      })
        .then((result) => {
          ;
          if (result.data.code == "Q001") {
            if (result.data.result.length == 0) {
              this.$toast("");
              return;
            }
          }
          this.show = true;
          this.eachdata = result.data.result;
        })
        .catch((err) => {
          ;
        });
    },
  
  },
};
</script>

<style lang="less" scoped>
@font-face {
  font-family: "../../assets/font/iconfont";
  src: url("../../assets/font/iconfont.eot");
  src: url("../../assets/font/iconfont.eot?#iefix") format("embedded-opentype"),
    url("../../assets/font/iconfont.woff2") format("woff2"),
    url("../../assets/font/iconfont.woff") format("woff"),
    url("../../assets/font/iconfont.ttf") format("truetype"),
    url("../../assets/font/iconfont.svg#iconfont") format("svg");
}
</style>