<template>
  <div class="from">
    <div class="fromtitle">
      <van-nav-bar title="" fixed></van-nav-bar>
    </div>
    <div class="from-body">
      <van-tabs animated sticky offset-top="50">
        <van-tab v-for="(value,index) in typedata" :key="index">
          <template #title>
            <div class="typeDesc" @click="cli(value)">
              <div>{{value.title}}</div>
            </div>
          </template>
          <div class="product">
            <div
              class="products"
              v-for="(v,i) in dataobj.products"
              :key="i"
              @click="goparticulars(v.oid)"
            >
              <div class="pro-box">
                <div class="box-title">
                  <div class="from-num">:{{v.oid}}</div>
                  <div class="underway">{{v.underway}}</div>
                </div>
                <div class="box-body">
                  <div class="boxbody-left">
                    <div class="trade-name"></div>
                    <div class="product-name">{{v.name}}</div>
                    <div class="product-price">{{v.prices}}</div>
                  </div>
                  <div class="boxbody-right">
                    <div class="purchase-time">{{v.createdAt | formatDate('yyyy-MM-dd hh:mm:ss')}}</div>
                    <div class="again-purchase">
                      <van-button
                        type="default"
                        @click.stop="sure(v)"
                        v-if="v.status==1? true:false"
                      ></van-button>
                      <van-button
                        type="default"
                        @click.stop="dele(v)"
                        v-if="v.status==1? false:true"
                      ></van-button>
                      <van-button type="default" @click.stop="againshop(v.oid)"></van-button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      status: false,
      sidarrs: [],
      statusnum: 0,
      typedata: [
        { title: "", status: 0 },
        { title: "", status: 1 },
        { title: "", status: 2 },
      ],
      // 
      dataobj: {
        products: [],
        product: [],
      },

      // 
    };
  },
  created() {
    this.acquirefrom(this.statusnum);
  },
  methods: {
    // 
    cli(value) {
      ;
      this.statusnum = value.status;

      this.acquirefrom(this.statusnum);
    },
    // 
    acquirefrom(status) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return 
      }
      this.axios({
        method: "GET",
        url: "findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status,
        },
      })
        .then((result) => {
          ;

          if (result.data.code == 70000) {
            // .oid
            // ,
            result.data.result.reverse();
            //  ==1
            result.data.result = result.data.result.sort((a, b) => {
              return a.status - b.status;
            });
            let types = [];

            result.data.result.map((v) => {
              if (types.indexOf(v.oid) === -1) {
                types.push(v.oid);
              }
            });

            let products = {};
            types.map((v) => {
              products[v] = [];
              result.data.result.map((item) => {
                if (v == item.oid) {
                  products[v].push(item);
                }
              });
            });

            this.dataobj.product = products;

            let dataresult = [];

            for (let key in products) {
              // 
              let obj = {};
              obj.name = [];
              obj.count = [];
              obj.price = [];
              obj.pid = [];
              obj.id = [];
              obj.img = [];
              obj.prices = 0;

              products[key].map((v) => {
                obj.createdAt = v.createdAt;
                obj.createdAt = v.createdAt;
                obj.oid = v.oid;
                obj.status = v.status;

                obj.prices += v.count * v.price;
                obj.underway = v.status == 1 ? "" : "";
                obj.name.push(v.name);
                obj.count.push(v.count);
                obj.price.push(v.price);
                obj.pid.push(v.pid);
                obj.id.push(v.id);
                obj.img.push(v.smallImg);
              });
              if (obj.name.length >= 2) {
                obj.name = obj.name[0] + "";
              } else {
                obj.name = obj.name[0];
              }
              dataresult.push(obj);
            }
            ;
            this.dataobj.products = dataresult;
          }
        })
        .catch((err) => {
          ;
        });
    },
    // 
    sure(value) {
      let url = "/receive";
      ;
      Dialog.confirm({
        title: "",
        message: "",
      })
        .then(() => {
          ;
          this.surefn(value, url);
          this.acquirefrom(this.statusnum);
        })
        .catch(() => {
          ;
        });
    },
    // 
    dele(value) {
      let url = "/removeOrder";
      Dialog.confirm({
        title: "",
        message: "",
      })
        .then(() => {
          ;
          this.surefn(value, url);
        })
        .catch(() => {
          ;
        });
    },

    // /
    surefn(value, url) {
      ;
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      //
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        oid: value.oid,
      });

      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url,
        data,
      })
        .then((result) => {
          this.$toast.clear();

          ;
          this.acquirefrom(this.statusnum);
        })
        .catch((err) => {
          this.$toast.clear();
          ;
        });
    },
    // 
    goparticulars(item) {
      let obj = this.dataobj.product[item];
      this.$router.push({ name: "fromparticulars", query: { obj } });
    },
    // 
    // 
    againshop(item) {
      // sid,pid..sid
      // .sid

      // 
      let all = [];
      // sid
      this.sidarrs = [];

      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$router.push({ name: "Login" });
        return;
      }
      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let arr = this.dataobj.product[item];
   
      // ;

      // 
      arr.forEach((v) => {
        let data = utils.queryString({
          appkey: this.appkey,
          pid: v.pid,
          tokenString,
          count: v.count,
          rule: v.rule
        });
        let axios = this.axios({
          method: "POST",
          url: "/addShopcart",
          //getparams, post,data
          data,
        })
          .then((result) => {
            this.$toast.clear();
            if (result.data.code == 3000) {
              let sid = result.data.sid;
              this.sidarrs.push(sid);
            }
            ;
            this.$toast(result.data.msg);
          })
          .catch((err) => {
            this.$toast.clear();
            return
            ;
          });
        all.push(axios);
      });
      // sid  ,
      // allaxiossid
      Promise.all(all)
        .then(() => {
          let obj = JSON.stringify(this.sidarrs);
          let ischeaks=true
          // 
           console.log(obj);
          this.$router.push({ name: "Tijiao", query: { obj,ischeaks } });
          // bug  
          //  sid
          // 

          //  
        })
        .catch((shibai) => {
         
        });
    },
  
  },
};
</script>

<style lang="less" scoped>
.from {
  min-height: 100vh;
  padding-bottom: 70px;
  background-color: #f4f4f4;
  .fromtitle {
    height: 50px;
    /deep/ .van-nav-bar {
      height: 50px;
      border-bottom: 1px solid #f4f4f4;
    }
  }
  .from-body {
    /deep/ .van-tabs {
      .van-tab {
        .van-tab__text {
          width: 100%;
          height: 100%;

          .typeDesc {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
          }
        }
      }
      .van-sticky {
        margin-bottom: 15px;
      }
      .van-tabs__content {
        width: 100%;
        box-sizing: border-box;
        padding: 0 10px;
        .product {
          .products {
            width: 100%;

            border-radius: 5px;
            margin-bottom: 10px;
            .pro-box {
              background-color: white;
              box-sizing: border-box;
              padding: 10px;
              border-bottom: 15px solid #f4f4f4;
              .box-title {
                display: flex;
                justify-content: space-between;
                border-bottom: 1px solid #e5e5e5;
                padding: 5px 0;
                .from-num {
                  color: #a0a0a0;
                  font-weight: 600;
                }
                .underway {
                  color: #2447be;
                  font-weight: 600;
                }
              }
              .box-body {
                display: flex;
                justify-content: space-between;
                padding: 10px 0;
                .boxbody-left {
                  div {
                    text-align: left;
                    font-size: 14px;
                    font-weight: 700;
                    margin-bottom: 10px;
                    color: #212121;
                  }
                }
                .boxbody-right {
                  display: flex;
                  flex-direction: column;
                  justify-content: space-between;
                  .purchase-time {
                    font-size: 8px;
                    color: #afafaf;
                  }
                  .again-purchase {
                    .van-button {
                      height: 30px;
                      border-color: #dcdcdc;
                      border-radius: 10px;
                      background-color: white;
                      &:first-child {
                        color: white;
                        background-color: #0b37be;
                        margin-right: 10px;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>