<template>
  <div class="Safety">
    <van-nav-bar title="" left-text="" @click-left="back" fixed></van-nav-bar>
    <div class="infoset">
      <van-cell
        :title="value.title"
        is-link
        @click="myinfo(value.title)"
        :to="{name:value.name}"
        v-for="(value,index) in infomains"
        :key="index"
      />
    </div>
    <van-button type="primary" @click="backinfo"></van-button>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      infomains: [
        { title: "", name: "" },
        { title: "", name: "" },
        { title: "", name: "" },
      ],
    };
  },
  created() {
    let tokenString = localStorage.getItem("CSTK");
    if (!tokenString) {
      return this.$router.push({ name: "Login" });
    }
  },
  methods: {
    back() {
      this.$router.push({ name: "My" });
    },
    myinfo(value) {
      if (value == "") {
        ;
      }
      if (value == "") {
        let url = "/destroyAccount";
        Dialog.confirm({
          title: "",
          message: "?!",
        })
          .then(() => {
            ;
            this.set(url);
          })
          .catch(() => {
            // on cancel
            ;
          });
      }
   
      if (value == "") {
        this.$router.push({ name: "setpasswo" });
      }
      if (value == "") {
        Dialog.confirm({
          title: "",
          message: "?",
        })
          .then(() => {
            this.$router.push({ name: "Login" });
          })
          .catch(() => {
            // on cancel
            ;
          });
      }
    },
    // 
    set(url) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
      });
      this.axios({
        method: "POST",
        url,
        data,
      })
        .then((result) => {
          ;
          if (result.data.code == "G001" || result.data.code == "F001") {
            localStorage.removeItem("CSTK");
            this.$router.push({ name: "Login" });
          }
          this.$toast(result.data.msg);
        })
        .catch((err) => {
          ;
        });
    },
    backinfo(){
  let url = "/logout";
        Dialog.confirm({
          title: "",
          message: "?",
        })
          .then(() => {
            ;

            this.set(url);
          })
          .catch(() => {
            // on cancel
            ;
          });
    }
  },
};
</script>

<style lang="less" scoped>
.Safety {
  padding: 50px 10px 0;
  /deep/ .van-cell {
    margin-bottom: 10px;
    background-color: #182094;
    color: white;
  }
  /deep/ .van-button{
    width: 80%;
    background-color: #ee0a24;
    border-color: #ee0a24;
    border-radius: 20px;
    margin-top: 100px;
  }
}
</style>